import React, { useState } from "react";
import { Input, TextareaAutosize ,  Button, IconButton, LinearProgress } from "@mui/material";
import { PhotoCamera } from "@mui/icons-material";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";



import { storage, db } from "./../../firebase";
import firebase from 'firebase/compat/app';



export default function Upload({ username }) {

  const [category, setCategory] = useState("");

  const [programTitle, setProgramTitle] = useState("");
  const [projectTitle, setProjectTitle] = useState("");


  const [projectLeader, setProjectLeader] = useState("");
  const [agency, setAgency] = useState("");
  const [coopagency1, setCoopAgency1] = useState("");
  const [coopagency2, setCoopAgency2] = useState("");
  const [coopagency3, setCoopAgency3] = useState("");
  const [coopagency4, setCoopAgency4] = useState("");
  const [coopagency5, setCoopAgency5] = useState("");

  const [site1, setSite1] = useState("");
  const [site2, setSite2] = useState("");

  const [type, setType] = useState("");

  const [rationale, setRationale] = useState("");




  const [methodology, setMethodology] = useState("");


  const [createdAt, setCreatedAt] = useState(new Date());
  const [imagePreview, setImagePreview] = useState('');
  const [image, setImage] = useState("");
  const [url, setUrl] = useState("");
  const [progress, setProgress] = useState(0);


  function handlePreview(image) {
    setImagePreview(URL.createObjectURL(image));
    setImage(image);
  }

  const handleChange = (e) => {
    if (e.target.files[0]) {
      handlePreview(e.target.files[0]);
    }
  };
  console.log('preview:', imagePreview);


  function handleUpload() {
    const uploadTask = storage.ref(`projectFile/${image.name}`).put(image);
    console.log("image:", image);
    console.log("image name:", image.name);
    uploadTask.on(
      "state_changed",
      (snapshot) => {
        //progress function
        const progress = Math.round(
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100
        );
        setProgress(progress);
      },
      (error) => {
        //error function
        console.log(error);
        alert(error.message);
      },
      () => {
        //complete function
        storage
          .ref("projectFile")
          .child(image.name)
          .getDownloadURL()
          .then((url) => {
            //post image inside db
            console.log("url:" + url);
            db.collection("projects").add({

              category: category,

              programTitle: programTitle,

              projectTitle: projectTitle,
              projectLeader: projectLeader,
              agency: agency,

              coopagency1: coopagency1,
              coopagency2: coopagency2,
              coopagency3: coopagency3,
              coopagency4: coopagency4,
              coopagency5: coopagency5,


              site1: site1,
              site2: site2,


              type: type,
              rationale: rationale,
              methodology: methodology,




              createdAt: createdAt,






              imageUrl: url,
          //    createdAt: firebase.firestore.FieldValue.serverTimestamp(),

            });
          });



          setCategory("");
          setProgramTitle("");

          setProjectTitle("");
          setProjectLeader("");
          setAgency("");



          setCoopAgency1("");
                setCoopAgency2("");
                      setCoopAgency3("");
                            setCoopAgency4("");
                                  setCoopAgency5("");

          setSite1("");
          setSite2("");

          setType("");
          setRationale("");
          setMethodology("");


          setCreatedAt("");

        setProgress(0);
        setImage(null);
        setImagePreview('');
      }
    );
  }

  return (
    <>


      <div >
              <h1>Upload Project </h1>






        <select onChange={(e) => setCategory(e.target.value)}>
        <option name="category" id="category" value="">Select Category</option>
       <option name="category" id="category" value="CLSD project">CLSD Project</option>
       <option name="category" id="category" value="Research and Development ">Research and Development </option>
       <option name="category" id="category" value="In-house Research">In-house Research</option>
       <option name="category" id="category" value="Others"> Others</option>
       </select>
       <br></br>

        <TextareaAutosize
          maxRows={2}
          aria-label="Enter program title"
          minRows={10}
          placeholder="Enter program title "
          style={{ width: 450 }}
          value={programTitle}
          onChange={(e) => setProgramTitle(e.target.value)}
          className="input"
          />

        <br></br>

        <TextareaAutosize
          maxRows={2}
          aria-label="Enter project title"
          minRows={10}
          placeholder="Enter project title"
          style={{ width: 450 }}
          value={projectTitle}
          onChange={(e) => setProjectTitle(e.target.value)}
          className="input"
          />

         <br></br>

        <TextareaAutosize
          maxRows={2}
          aria-label="Enter project leader "
          minRows={10}
          placeholder="Enter project leader "
          style={{ width: 450 }}
          value={projectLeader}
          onChange={(e) => setProjectLeader(e.target.value)}
          className="input"
          />
             <br></br>

             <TextareaAutosize
               maxRows={2}
               aria-label="Enter agency"
               minRows={10}
               placeholder="Enter agency "
               style={{ width: 450 }}
               value={agency}
               onChange={(e) => setAgency(e.target.value)}
               className="input"
               />


     <br></br>
     <TextareaAutosize
       maxRows={2}
       aria-label="Enter Cooperating agency"
       minRows={10}
       placeholder="Enter Cooperating agency "
       style={{ width: 450 }}
       value={coopagency1}
       onChange={(e) => setCoopAgency1(e.target.value)}
       className="input"
       />

       <br></br>
       <TextareaAutosize
         maxRows={2}
         aria-label="Enter Cooperating agency"
         minRows={10}
         placeholder="Enter Cooperating agency "
         style={{ width: 450 }}
         value={coopagency2}
         onChange={(e) => setCoopAgency2(e.target.value)}
         className="input"
         />

         <br></br>
         <TextareaAutosize
           maxRows={2}
           aria-label="Enter Cooperating agency"
           minRows={10}
           placeholder="Enter Cooperating agency "
           style={{ width: 450 }}
           value={coopagency3}
           onChange={(e) => setCoopAgency3(e.target.value)}
           className="input"
           />

           <br></br>
           <TextareaAutosize
             maxRows={2}
             aria-label="Enter Cooperating agency"
             minRows={10}
             placeholder="Enter Cooperating agency "
             style={{ width: 450 }}
             value={coopagency4}
             onChange={(e) => setCoopAgency4(e.target.value)}
             className="input"
             />
             <br></br>
             <TextareaAutosize
               maxRows={2}
               aria-label="Enter Cooperating agency"
               minRows={10}
               placeholder="Enter Cooperating agency "
               style={{ width: 450 }}
               value={coopagency5}
               onChange={(e) => setCoopAgency5(e.target.value)}
               className="input"
               />

<br></br>

        <TextareaAutosize
          maxRows={2}
          aria-label="Enter site "
          minRows={10}
          placeholder="Enter site"
          style={{ width: 450 }}
          value={site1}
          onChange={(e) => setSite1(e.target.value)}
          className="input"
          />

          <br></br>

                  <TextareaAutosize
                    maxRows={2}
                    aria-label="Enter site "
                    minRows={10}
                    placeholder="Enter site"
                    style={{ width: 450 }}
                    value={site2}
                    onChange={(e) => setSite2(e.target.value)}
                    className="input"
                    />

     <br></br>
     <TextareaAutosize
       maxRows={2}
       aria-label="Enter rationale "
       minRows={10}
       placeholder="Enter rationale"
       style={{ width: 450 }}
       value={rationale}
       onChange={(e) => setRationale(e.target.value)}
       className="input"
       />


     <br></br>

        <TextareaAutosize
          maxRows={2}
          aria-label="Enter methodoloogy "
          minRows={10}
          placeholder="Enter methodoloogy"
          style={{ width: 450 }}
          value={methodology}
          onChange={(e) => setMethodology(e.target.value)}
          className="input"
          />





        <DatePicker
        selected={createdAt}
        onChange={date => setCreatedAt(date)}
        />


     <br></br>
        <input
          type="file"
          onChange={handleChange}
          className="input"
          style={{ display: "none" }}
          id="raised-button-file"
        />
        <label htmlFor="raised-button-file">
          <IconButton
            variant="raised"
            component="span"
            className="button"
            color="primary"
          >
          <img
        src={imagePreview||"https://www.brno.cz/o/brno-theme/assets/images/placeholders/avatar/avatar-unisex.png?v=0.1.0"}
        alt=''
          className="image"
        width="150"
        height="150"
      />
            <PhotoCamera />
          </IconButton>
        </label>
        <LinearProgress
          variant="determinate"
          color="primary"
          value={progress}
            className="progress"
        />
        <Button variant="outlined" onClick={handleUpload}>
          Upload
        </Button>
      </div>
    </>
  );
}
